import Vuex from 'vuex';
import mutations from './mutations';
import actions from './actions';
import getters from './getters';

const store = new Vuex.Store({
  state: {
    view: 'default', /** default | conventional | oleic */
    selectedState: {
      id: null,
      name: '',
      farmsCount: 0,
      acres: 0,
      bushels: 0,
      proccessingPlantsCount: 0
    },
    showInfo: false,
    texts: {
      conventional: {
        title: 'Conventional soybean growing regions',
        body: 'Soybeans are the second largest crop grown on U.S. soil, and soybean oil is the most widely used edible oil in the U.S. Ninety-eight percent of U.S. soybeans are grown sustainably and farmers employ practices such as crop rotation, reduced tillage and water management to enrich soil quality, conserve water, and boost crop productivity.',
        statistic: [
          { label: 'Plant', value: 'MAY-JUNE' },
          { label: 'Harvest', value: 'SEPT-OCT' },
          { label: 'States Currently Grow High Oleic Soybeans', value: '11' },
          { label: 'U.S Soybean Farmers', value: '550,000' },
        ]
      },
      oleic: {
        title: 'high oleic soybean growing regions',
        body: 'High oleic soybeans were developed to meet the food industry’s need for sustainable PHO replacements. Their broad growing region contributes to a more stable production volume from crop year to crop year. Given current projections, high oleic soybeans are set to be America’s fourth largest row crop in acreage by 2023.',
        statistic: [
          { label: 'Plant', value: 'MAY-JUNE' },
          { label: 'Harvest', value: 'SEPT-OCT' },
          { label: 'States Currently Grow High Oleic Soybeans', value: '11' },
          { label: 'Acres of High Oleic Soybeans Planted by 2023', value: '18,000,000' },
        ]
      },
    },
    popup: {
      id: 0,
      isOpen: false
    },
    popupData: {
      0: {
        name: 'ANNIE DEE',
        body: 'USB Farmer-Director Annie Dee grows soybeans, corn, cattle and pine trees with her brother and two sons on the Dee River Ranch in Alabama. They farm in a sustainable way to protect the land for this generation and many generations to come, and manage the largest cultivated field east of the Mississippi delta – it’s 2,000 acres and covers two states! Click on the video to learn more.',
        ownerships: 4,
        acres: '10,000',
        ytLink: 'https://www.youtube.com/watch?v=kg0GkKouKVc'
      },
      1: {
        name: 'MAX LUCKY',
        body: 'USB Farmer-Director Annie Dee grows soybeans, corn, cattle and pine trees with her brother and two sons on the Dee River Ranch in Alabama. They farm in a sustainable way to protect the land for this generation and many generations to come, and manage the largest cultivated field east of the Mississippi delta – it’s 2,000 acres and covers two states! Click on the video to learn more.',
        ownerships: 5,
        acres: '10,000,000',
        ytLink: 'https://www.youtube.com/watch?v=V_1jRSnHsgc'
      },
      2: {
        name: 'FEELS BAD MAN',
        body: 'USB Farmer-Director Annie Dee grows soybeans, corn, cattle and pine trees with her brother and two sons on the Dee River Ranch in Alabama. They farm in a sustainable way to protect the land for this generation and many generations to come, and manage the largest cultivated field east of the Mississippi delta – it’s 2,000 acres and covers two states! Click on the video to learn more.',
        ownerships: 14,
        acres: '14,898',
        ytLink: 'https://www.youtube.com/watch?v=ZLUrqTQH3-k'
      }
    }
  },
  actions,
  mutations,
  getters
});

export default store;


