<template>
    <div class="map-wrapper">
      <steps></steps>
      <div id="map-wr" name="main-parent" 
      :class=" { hide: currentView !== 'default', 'overlay--conventional': currentView === 'conventional', 'overlay--oleic': currentView === 'oleic' } ">
        <div class="svg-map">
          <div id="map"></div>
        </div>
        <!--<div v-if="currentView === 'conventional'" >
          <div class="overlay overlay--conventional"></div>
        </div>
        <div v-else-if="currentView === 'oleic'" >
          <div class="overlay overlay--oleic"></div>
        </div>-->
      </div>
      <div class="map-legend" v-if="currentView === 'oleic'">
        <div class="map-legend-item">
          <div class="map-legend-item__icon"></div>
          <div class="map-legend-item__text">current high oleic soybean growing regions</div>
        </div>
        <div class="map-legend-item">
          <div class="map-legend-item__icon map-legend-item__icon--projected"></div>
          <div class="map-legend-item__text">projected 2023 high oleic soybean growing regions</div>
        </div>
      </div>

      <div class="map-legend" v-if="currentView === 'conventional'">
        <div class="map-legend-item">
          <div class="map-legend-item__icon"></div>
          <div class="map-legend-item__text">conventional soybean growing regions</div>
        </div>
      </div>
      <toggle></toggle>
      <popup-component></popup-component>
    </div>
</template>

<script>
import StepsComponent from './Steps.vue';
import ToggleComponent from './Toggle.vue';
import PopupComponent from './../Popup.vue';

import { random, findParentNode } from './../../utils/index.js';

export default {
  components: {
    'popup-component': PopupComponent,
    'steps': StepsComponent,
    'toggle': ToggleComponent
  },
  data: function () {
    return {
      lastStateId: 0,
      mapStates: {},
      locations: {},
    }
  },
  computed: {
    currentView: {
      get () {
        return this.$store.getters.getCurrentView;
      }
    },
  },
  watch: {
    currentView: function (val) {
      this.setActiveCoverageMode(this.currentView !== 'default');
      this.setActiveLocationPins(!(this.currentView !== 'default'));
    }
  },
  created: function () {
    setTimeout(() => {
      this.mapStates = document.getElementsByTagName('path'); 
      this.locations = document.getElementsByTagName('image'); 

      for (let tag in this.mapStates) {
        let elem = this.mapStates[tag];
        if (typeof elem === 'object') {
          elem.onmouseover = (e) => {
            let parent = findParentNode('main-parent', elem);
            if (parent.classList.contains('hide')) {
              elem.classList.add('covered--hover');
            }

            elem.style.fill = '#64A733';
            // elem.style.transition = '.3s ease';
          };

          elem.onmouseout = () => {
            let parent = findParentNode('main-parent', elem);
            if (parent.classList.contains('hide')) {
              elem.classList.remove('covered--hover');
            }

            if (!parent.classList.contains('active')) {
              elem.style.fill = '#fff';
            }
          }
        }
      }
    }, 1000);
    
    this.$map.hooks.click_location = (id) => {
      this.$store.commit('OPEN_POPUP', id);
    }

    this.$map.hooks.click_state = (id) => {
      let state = this.$mapData.state_specific[id];
      let stateName = state.name;
      
      if (id !== this.lastStateId || this.currentView !== 'default') {
        document.getElementsByClassName('sm_state_' + id)[0].style.fill = '#64A733';
        document.getElementsByClassName('sm_state_' + id)[0].classList.add('active');
        
        if (this.lastStateId !== 0) {
          document.getElementsByClassName('sm_state_' + this.lastStateId)[0].style.fill = '#fff';
          document.getElementsByClassName('sm_state_' + this.lastStateId)[0].classList.remove('active');
        }
        
        this.$store.dispatch('random_state_info', {
          id: id,
          name: stateName,
          farmsCount: random(1, 1000000),
          acres: random(1, 1000000),
          bushels: random(1, 1000000),
          proccessingPlantsCount: random(1, 1000000)
        });

        this.$store.commit('SET_VIEW', 'default');

        this.lastStateId = id;
      }
    }
  },
  methods: {
    setActiveLocationPins: function (val) {
      for (let image in this.locations) {
        let pin = this.locations[image]; 
        if (typeof pin === 'object') pin.style.display = val ? 'block' : 'none';
      }
    },
    setActiveCoverageMode: function (val) {
      for (let tag in this.mapStates) {
        let state = this.mapStates[tag]; 

        if (typeof state === 'object') {
          if (val) state.classList.add('covered');
          else {
            if (state.classList.contains('covered')) {
              const removeClasses = ['covered', 'covered--hover'];
              state.classList.remove(...removeClasses);
            }
          }

          // state.style.transition = '.3s ease'
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">  
.fade-enter-active, .fade-leave-active {
  // transition: opacity .3s ease;
}

.fade-enter, .fade-leave-to {
  // opacity: 0
}

#map-wr {
  position: relative;
  min-height: 596px;

  background: {
    repeat: no-repeat;
    size: contain;
  }

  @media screen and (max-width: 778px) {
    min-height: auto;
  }

  &.overlay { 
    &--conventional {
      background-image: url(../../assets/coverages/us-01.svg);
    }

    &--oleic {
      background-image: url(../../assets/coverages/us-02.svg);
    }
  }
}

.map-wrapper {
  position: relative;
  padding: 35px 0;

  background: {
    image: url(../../assets/bg-block.png);
    repeat: no-repeat;
    size: cover;
  }

  &:before {
    content: '';
    position: absolute;
    opacity: 0.5;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #154835;
    z-index: 0;
  }

  div {
    z-index: 91;
  }
}

.svg-map {
  position: relative;
  z-index: 999 !important;
}

.map-legend {
  position: absolute;
  left: 50px;
  width: 220px;
  height: 64px;
  bottom: 30%;

  @media screen and (max-width: 580px) {
    width: 100%;
    bottom: auto;
    left: 0;
    padding: 0 20px;
  }
  
  &-item {
    height: 25px;

    &__icon {
      float: left;
      height: 25px;
      width: 25px;
      background: {
        image: url(../../assets/filter-1.png);
        repeat: no-repeat;
        size: cover;
      };

      &--projected {
        background-image: url(../../assets/filter-2.png);
      }
    }

    &__text {
      padding-left: 35px;
      color: #fff;
      font-weight: bold;
      font-size: 12px;
      line-height: 12px;
      text-transform: uppercase;
      text-align: left;
    }

    &:first-child {
      margin-bottom: 15px;
    }
  }
}

</style>



