<template>
  <div class="info" v-if="canShowInfo">
    <div class="info-window">
      <div class="info-window-header" v-html="content.title"></div>
      <div class="info-window-body">
        <div v-if="currentView !== 'default'" class="info-description">
          <p v-if="currentView === 'conventional'" v-html="content.body"></p>
          <p v-else v-html="content.body"></p>
        </div>
        <div v-if="currentView === 'default'" class="info-statistic">
          <div class="info-statistic-item">
            <div class="info-statistic-item__title">Number of soybean Farms</div>
            <div class="info-statistic-item__value">{{ selectedState.data.farmsCount }}</div>
          </div>
          <div class="info-statistic-item">
            <div class="info-statistic-item__title">Acres</div>
            <div class="info-statistic-item__value">{{ selectedState.data.acres }}</div>
          </div>
          <div class="info-statistic-item">
            <div class="info-statistic-item__title">Bushels</div>
            <div class="info-statistic-item__value">{{ selectedState.data.bushels }}</div>
          </div>
          <div class="info-statistic-item">
            <div class="info-statistic-item__title">Number of Soybean Processing plants</div>
            <div class="info-statistic-item__value">{{ selectedState.data.proccessingPlantsCount }}</div>
          </div>
        </div>
        <div v-else class="info-statistic">
          <div v-for="item in content.statistic" :key="item.label" class="info-statistic-item">
            <div class="info-statistic-item__title">{{ item.label }}</div>
            <div class="info-statistic-item__value">{{ item.value }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    canShowInfo: {
      get () {
        return this.$store.getters.getShowInfo;
      }
    },
    selectedState: {
      get () {
        return this.$store.getters.getSelectedState;
      }
    },
    currentView: {
      get () {
        return this.$store.getters.getCurrentView;
      }
    },
    content: {
      get () {
        if (this.currentView === 'default') {
          return { 
            title: 'State soybean crop profile: <span>' + this.selectedState.data.name + '</span>'
          };
        } else if (this.currentView === 'conventional') {
          return {
            title: this.$store.getters.getTexts.conventional.title,
            body: this.$store.getters.getTexts.conventional.body,
            statistic: this.$store.getters.getTexts.conventional.statistic,
          };
        } else {
          return {
            title: this.$store.getters.getTexts.oleic.title,
            body: this.$store.getters.getTexts.oleic.body,
            statistic: this.$store.getters.getTexts.oleic.statistic,
          };
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .info {
    background: {
      image: url(../assets/bg-block-info.png);
      repeat: no-repeat;
      size: cover;
    }

    padding: {
      top: 45px;
      left: 38px;
      right: 38px;
      bottom: 190px;
    }
    
    @media screen and (max-width: 580px) {
      padding: {
        left: 15px;
        right: 15px;
      }
    }
  }

  .info-window {
    &-header {
      background-color: #64A733;
      width: 100%;
      height: 75px;
      color: #174835;
      font-family:  'Oswald', sans-serif;
      font-weight: bold;
      font-size: 36px;
      text-transform: uppercase;
      line-height: 75px;

      @media screen and (max-width: 768px) {
        height: auto;
        padding: 30px 0;
        font-size: 24px; 
        line-height: normal; 
      }

      span {
        color: #fff;
      }
    }

    &-body {
      background-color: rgba(255, 255, 255, 0.9);
      padding-bottom: 60px;
      @media screen and (max-width: 580px) {
        padding-bottom: 20px;
      }
    }
  }

  .info-description {
    p {
      font-size: 20px;
      line-height: 30px;
      color: #65676A;
      text-align: left;
      padding: 20px 80px 0;
      @media screen and (max-width: 580px) {
        padding: 20px 15px 0;
        font-size: 16px;
      }
    }
  }
  
  .info-statistic {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @media screen and (max-width: 480px) {}
    @media screen and (max-width: 768px) {
      padding: 0 20px;
    }
    
    &-item {
      width: 50%;
      margin-top: 40px;

      @media screen and (max-width: 480px) {
        width: 100%;
        margin-top: 20px;
      }

      &__title {
        padding: 0 100px;
        color: #174835;
        font-size: 18px;
        line-height: 22px;
        font-weight: bold;

        @media screen and (max-width: 768px) {
          padding: 0;
        }
      }

      &__value {
        font-size: 58px;
        line-height: 70px;
        font-family:  'Oswald', sans-serif;
        color: #64A733;
        @media screen and (max-width: 580px) {
          font-size: 36px; 
        }
      }
    }
  }
</style>


