<template>
  <div class="intro">
    <h1 class="intro__title">{{ intro.title }}</h1>
    <p class="intro__body">{{ intro.body }}</p>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      intro: {
        title: 'U.S. SOYBEAN GROWERS',
        body: 'Nearly every farm in the United States is family owned. U.S. soybean farms are thriving and will continue to keep up with industry demand for U.S.-grown soy solutions, including high oleic soybean oil, a drop-in trans fat-free replacement for partially hydrogenated oils.'
      }
    }
  }
}
</script>

<style lang="scss">

$font-family-default: 'Open Sans', sans-serif;
$font-family-heading: 'Oswald', sans-serif;
$color-brand: #64A733;
$color-text: #65676A;
$font-size-intro-heading: 50px;
$font-size-intro-body: 22px;

.intro {
  padding-top: 60px;
  @media screen and (max-width: 580px) {
    padding: 60px 30px 0; 
  }
  
  &__title {
    color: $color-brand;
    font-size: $font-size-intro-heading;
    font-family: $font-family-heading;
    font-weight: bold;
  }

  &__body {
    color: $color-text;
    font-size: $font-size-intro-body;
    line-height: 40px;
    text-align: left;
    padding: 0 100px 50px;

    @media screen and (max-width: 580px) {
      padding: 0 0 50px;
    }
  }
}
</style>



