/**
 * Function search and return a nth-parent
 * @param {String} parentName 
 * @param {Object} childObj 
 */
const findParentNode = (parentName, childObj) => {
  let testObj = childObj.parentNode;
  let count = 1;

  while (testObj.getAttribute('name') != parentName) {
    testObj = testObj.parentNode;
    count++;
  }

  return testObj;
};

/**
 * Function return a random number between min and max values  
 * @param {Number} min 
 * @param {Number} max 
 */
const random = (min, max) => Math.floor(min + Math.random() * (max + 1 - min));

export { findParentNode, random };


