<template>
  <div v-if="popup.isOpen" class="popup">
    <div class="popup-header">
      <div class="popup-header__title">{{ data.name }}</div>
      <div @click="closePopup" class="popup-header__close">CLOSE</div>
    </div>
    <div class="popup__youtube">
      <div class="popup__youtube-loader"></div>
      <iframe width="100%" height="380" :src="'https://www.youtube.com/embed/' + data.ytLink.split('?v=')[1]" frameborder="0" allowfullscreen></iframe>
    </div>
    <div class="popup__body">
      {{ data.body }}
    </div>
    <div class="popup-stats">
      <div class="popup-stats-item">
        <div class="popup-stats-item__value">{{ data.ownerships }}</div>
        <div class="popup-stats-item__label">Generations of Family Ownership</div>
      </div>
      <div class="popup-stats-item">
        <div class="popup-stats-item__value">{{ data.acres }}</div>
        <div class="popup-stats-item__label">Acres</div>
      </div>
    </div>
    <div class="popup__button">
      <a href="#">ASK {{ data.name.split(' ')[0] }} A QUESTION</a>
    </div>
  </div>  
</template>

<script>
export default {
  name: 'popup',
  computed: {
    popup: {
      get () {
        return this.$store.getters.getPopup;
      }
    },
    data: {
      get () {
        return this.$store.getters.getPopupData[this.popup.id];
      }
    }
  },
  methods: {
    closePopup: function () {
      this.$store.commit('CLOSE_POPUP');
    }
  }
}
</script>

<style lang="scss" scoped>
  .popup {
    position: fixed;
    width: 710px;
    left: 50%;
    z-index: 9999;
    transform: translateX(-50%);
    background-color: #64A733;
    padding: 30px;
    top: 45px;
    border-radius: 10px;
    
    @media screen and (max-width: 768px) {
      position: absolute;
      width: 90%;
    }
  }

  .popup__body {
    font-size: 14px;
    line-height: 22px;
    color: #154835;
    text-align: left;
    padding: {
      top: 10px;
      bottom: 25px;
    }
  }

  .popup-stats {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 550px;
    left: 50%;
    margin-left: -275px;


    @media screen and (max-width: 768px) {
      width: 100%;
      left: auto;
      margin-left: 0;
      flex-wrap: wrap;
    }


    &-item {
      width: 240px;
      text-align: left;
      
      @media screen and (max-width: 768px) {
        width: 100%;
      }

      &__value {
        float: left;
        color: #fff;
        font-size: 60px;
        line-height: 74px;
        font-family: 'Oswald', sans-serif;
      } 

      &__label {
        position: relative;
        left: 15px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 22px;
        line-height: 24px;
        font-weight: bold;
        color: #154835;
      } 
    }
  }

  .popup-header {
    display: inline-block;
    width: 100%;
    padding-bottom: 25px;
    color: #fff;

    @media screen and (max-width: 768px) {
      padding-bottom: 0;
    }

    &__title {
      float: left;
      font-size: 36px;
      line-height: 36px;
      font-family: 'Oswald', sans-serif;
      @media screen and (max-width: 580px) {
        font-size: 24px;
      }

      &:after {
        content: '';
        display: table;
        clear: both;
      }
    }

    &__close {
      float: right;
      cursor: pointer;
      font-size: 22px;
      font-family: 'Oswald', sans-serif;
      font-weight: bold;
      padding: 0 25px;
      border: 2px solid #fff;
      border-radius: 10px;
      transition: .3s ease;
      @media screen and (max-width: 580px) {
        font-size: 16px;
        padding: 0 10px;
      }
      
      &:hover {
        background-color: #fff;
        color: #154835;
      }

      &:after {
        content: '';
        display: table;
        clear: both;
      }
    }
  }

  .popup__youtube {
    position: relative;
    min-height: 380px;

    iframe {
      position: relative;
    }
  }

  .popup__youtube-loader {
    position: absolute;
    height: 75px;
    width: 75px;
    top: 50%;
    left: 50%;
    margin: {
      top: -37.5px;
      left: -37.5px;
    };
    z-index: 0;
    -webkit-animation: spin 1s linear infinite;
    animation: spin 1s linear infinite;
    border-top: 3px solid #174835;
    border-radius: 50%;

    @-webkit-keyframes spin {
      to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
      }
    }

    @keyframes spin {
      to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
      }
    }
  }

  .popup__button {
    height: 70px;
    margin-top: 25px;

    @media screen and (max-width: 768px) {
      height: auto;
      margin-top: 5px;
    }

    a {
      font-family: 'Oswald', sans-serif;
      font-weight: bold;
      color: #fff;
      background-color: #154835;
      padding: 9px 50px;
      text-transform: uppercase;
      border-radius: 10px;
      font-size: 36px;
      text-decoration: none;

      @media screen and (max-width: 768px) {
        padding: 10px 0;
        font-size: 22px;
        width: 100%;
        float: left;
        margin-top: 20px;
      }
    }
  }
</style>



