export default {
  getSelectedState: state => {
    return state.selectedState;
  },
  getCurrentView: state => {
    return state.view;
  },
  getPopup: state => {
    return state.popup;
  },
  getPopupData: state => {
    return state.popupData;
  },
  getTexts: state => {
    return state.texts;
  },
  getShowInfo: state => {
    return state.showInfo;
  }
}


