<template>
  <div class="steps">
    <div v-for="item in steps" :key="item.id" class="steps-step">
      <div class="steps-step__id">{{ item.id }}</div>
      <div class="steps-step__title">{{ item.title }}</div>
      <div class="steps-step__body">{{ item.body }}</div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      steps: [
        {
          id: 1,
          title: 'Navigate',
          body: 'the U.S Soybean Growers map to learn state-by-state soybean statistics.'
        },
        {
          id: 2,
          title: 'Click',
          body: 'the location merkers to hear from U.S. soybean growers about a day in the life on their farm.'
        },
        {
          id: 3,
          title: 'Select',
          body: 'the "conventional soybean" and "high oleic soybean" growing regions map filters to see where various soybean traits are grown.'
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
  .steps {
    position: relative;   
    display: flex;
    justify-content: space-around ; 
    width: 100%;

    @media screen and (max-width: 580px) {
      padding: 0 30px;
    }

    @media screen and (max-width: 480px) {
      flex-wrap: wrap;
    }
  }

  .steps-step {
    display: inline-block;
    text-align: center;
    width: 230px;
    @media screen and (max-width: 480px) {
      margin-top: 30px;

      &:first-child {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 40px;
      }
    }
    
    .steps-step__id {
      display: inline-block;
      text-align: center;
      line-height: 45px;
      border-radius: 50%;
      background-color: #64A733;
      width: 45px;
      height: 45px;
      margin-bottom: 24px;
      font-size: 24px;
      color: #fff;

      @media screen and (max-width: 480px) {
        margin-bottom: 0;
      }
    }

    .steps-step__title {
      font-size: 35px;
      color: #ADCB48;
      font-family:  'Oswald', sans-serif;
      text-transform: uppercase;
    }

    .steps-step__body {
      font-size: 14px;
      line-height: 22px;
      color: #fff;
    }
  }
</style>



