<template>
  <div class="toggle">
    <div @click="() => { currentView = 'conventional'}"  :class="{ 'active' : isConventional }" class="toggle-item">
      <div class="toggle-item__switch"></div>
      <div class="toggle-item__map toggle-item__map--conv"></div>
      <div class="toggle-item__title">CONVENTIONAL SOYBEAN</div>
      <div class="toggle-item__region">Growing Regions</div>
    </div>  
    <div @click="() => { currentView = 'oleic'}" class="toggle-item" :class="{ 'active' : isOleic }">
      <div class="toggle-item__switch"></div>
      <div class="toggle-item__map toggle-item__map--oleic"></div>
      <div class="toggle-item__title">HIGH OLEIC SOYBEAN</div>
      <div class="toggle-item__region">Growing Regions</div>
    </div>
  </div>  
</template>

<script>
export default {
  data: function () {
    return {}
  },
  computed: {
    currentView: {
      get () {
        return this.$store.getters.getCurrentView;
      },
      set (value) {
       if (this.currentView === value) {
          this.$store.commit('SET_VIEW', 'default');
        } else {
          this.$store.commit('SET_VIEW', value);
        } 
      }
    },
    isConventional: {
      get () {
        return this.currentView === 'conventional';
      }
    },
    isOleic: {
      get () {
        return this.currentView === 'oleic';
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .toggle {
    display: flex;
    justify-content: space-between;
    width: 778px;
    margin: 50px auto 0 auto;
    user-select: none;
    @media screen and (max-width: 778px) {
      width: 100%;
    }
  }

  .toggle-item {
    position: relative;
    width: 360px;
    height: 200px;
    opacity: 0.5;

    cursor: pointer;
    transition: .2s ease;

    @media screen and (max-width: 580px) {
      width: 50%;
    }

    &:hover {
      opacity: 1;
    }

    &__title {
      position: relative;
      z-index: 10;
      display: inline-block;
      width: 100%;
      font-size: 28px;
      text-transform: uppercase;
      color: #FFD419;
      font-family:  'Oswald', sans-serif;

      @media screen and (max-width: 580px) {
        font-size: 18px;
      }
    }

    &__region {
      position: relative;
      z-index: 10;
      display: inline-block;
      width: 100%;
      font-size: 18px;
      font-weight: bold;
      color: #fff;
      
      @media screen and (max-width: 580px) {
        font-size: 12px;
      }
    }

    &__map {
      position: relative;
      display: inline-block;
      width: 200px;
      height: 125px;
      background: {
        size: cover;
        position: 50%;
        repeat: no-repeat;
      }

      &--conv {
        background-image: url('../../assets/coverage.png');
      }

      &--oleic {
        background-image: url('../../assets/coverage-2.png');
      }
      

    @media screen and (max-width: 580px) {
      display: none;
    }
  }

    &__switch {
      position: relative;
      float: left;
      width: 30px;
      height: 80px;
      border-radius: 100px;
      -webkit-box-shadow: inset 0px 0px 25px 0px rgba(0,0,0,0.2);
      -moz-box-shadow: inset 0px 0px 25px 0px rgba(0,0,0,0.2);
      box-shadow: inset 0px 0px 25px 0px rgba(0,0,0,0.2);
      left: 15px;
      transition: .3s ease;
      background: {
        image: url(../../assets/ui/arrow_down.png);
        color: #fff;
        position: 50% 80%;
        repeat: no-repeat;
      }

      @media screen and (max-width: 580px) {
        transform: rotate(-90deg) translateY(-15%) translateX(-50%);
        height: 50%;
        left: 50%;
      }

      &:after {
        position: absolute;
        content: '';
        top: 0;
        left: -3px;
        width: 30px;
        height: 30px;
        border: 3px solid #fff;
        border-radius: 50%;
        background-color: #FFD419;
        transition: all 0.3s ease;
        -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.4);
        -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.4);
        box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.4);
      }
    }

    &.active {
      opacity: 1;
      
      .toggle-item__switch {
        background: {
          image: url(../../assets/ui/arrow_up.png);
          color: #ADCB48;
          position: 50% 30%;
        };

        &:after {
          top: 100%;
          margin-top: -30px;
          background-color: #64A733;
        }
      }

      .toggle-item__title {
        color: #ADCB48;
      }
    }  
  }
</style>



