export default {
  'SELECT_STATE': (state, type) => {
    state.selectedState = type;
    state.showInfo = true;
  },
  'SET_VIEW': (state, type) => {
    state.view = type;
    
    if (type === 'default') {
      if (state.selectedState.id  === null) {
        state.showInfo = false;
      }
      else{
        state.showInfo = true;        
      }
    } else {
      state.showInfo = true;
    }
  },
  'SET_NAME': (state, type) => {
    state.stateName = type;
  },
  'OPEN_POPUP': (state, type) => {
    state.popup.isOpen = true;
    state.popup.id = type;
  },
  'CLOSE_POPUP': (state) => {
    state.popup.isOpen = false;
  },
  'TOGGLE_POPUP': (state) => {
    state.popupIsOpen = !state.popupIsOpen;
  }
}


